*{
    margin: 0;
    padding: 0;
}

.Container_List_Main{    
    position: fixed;    
    width: 100%;
    height: 60px;
    z-index: 999;
}

.Container_List_Main .Container_List{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 20px 0 20px;
}

.Container_List_Main .Container_List .Logo_Main{
    color: antiquewhite;
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    /*background: violet;
    border-radius: 20%;*/
}

.Container_List_Main .Container_List .Logo_Main .Logo_Main_Img{
    width: 40px;
    height: 40px;
    background-image: url('../img/Logo_vudu.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.Container_List_Main .Container_List .List_Main {
    display: flex;
    list-style: none;
    gap: 10px;
    color: aliceblue;
}