*{
    margin: 0;
    padding: 0;
}


.Container_Footer{
    background: linear-gradient(to top, rgb(0, 0, 0), rgb(4, 4, 46) 100%);
    height: 40vh;
}

.Container_Footer .Footer_List{
    display: flex;
    color: whitesmoke;
    justify-content: space-around;
    position: relative;
    top: 30%;
}

.Container_Footer .Footer_List .List p{
    font-size: 1.3rem;
    color: #f1ef76;
}

.Container_Footer .Footer_List .List ul{
    list-style: none;
    margin-top: 15px;
}